import React, { Component } from 'react';
import { Link, NavLink } from 'react-router-dom';
import { Badge, UncontrolledDropdown, DropdownItem, DropdownMenu, DropdownToggle, Nav, NavItem } from 'reactstrap';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import * as action from '../../redux/actions/authAction'
import { FormCheckbox } from "shards-react";
import { AppAsideToggler, AppNavbarBrand, AppSidebarToggler } from '@coreui/react';
import logo from '../../assets/img/brand/logo.svg'
import sygnet from '../../assets/img/brand/sygnet.svg'

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};

class DefaultHeader extends Component {
  render() {

    // eslint-disable-next-line
    const { children, ...attributes } = this.props;

    return (
      <React.Fragment>
        <AppSidebarToggler className="d-lg-none" display="md" mobile />
        <AppNavbarBrand
          full={{ src: logo, width: 89, height: 25, alt: 'CoreUI Logo' }}
          minimized={{ src: sygnet, width: 30, height: 30, alt: 'CoreUI Logo' }}
        />
        <AppSidebarToggler className="d-md-down-none" display="lg" />

        <Nav className="d-md-down-none" navbar>
          <NavItem className="px-3">
            <NavLink to="/scada/mixing" className="nav-link" >Mixing</NavLink>
          </NavItem>
          <NavItem className="px-3">
            <Link to="/scada/mobilerobot" className="nav-link">Mobile Robot</Link>
          </NavItem>
          <NavItem className="px-3">
            <NavLink to="/scada/robot" className="nav-link">Robot</NavLink>
          </NavItem>
        </Nav>
        <Nav className="ml-auto" navbar>
          {/* <NavItem className="d-md-down-none">
            <NavLink to="#" className="nav-link"><i className="icon-bell"></i><Badge pill color="danger">5</Badge></NavLink>
          </NavItem>
          <NavItem className="d-md-down-none">
            <NavLink to="#" className="nav-link"><i className="icon-list"></i></NavLink>
          </NavItem>
          <NavItem className="d-md-down-none">
            <NavLink to="#" className="nav-link"><i className="icon-location-pin"></i></NavLink>
          </NavItem> */}
          <UncontrolledDropdown nav direction="down">
            <DropdownToggle nav>
              <img src={require('../../images/img_design.png')} className="img-avatar" alt="Design Scada" />
            </DropdownToggle>
            <DropdownMenu right>
              <DropdownItem header tag="div" className="text-center" ><strong>Design Scada</strong></DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedAddPanel}
                  onChange={() => this.props.actionToggleAddPanel()}
                  >
                    Add Panel
                  </FormCheckbox>
                } 
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedEditPanel}
                    onChange={() => this.props.actionToggleEditPanel()}>
                    Edit Panel
                  </FormCheckbox>
                }
              </DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedAddImage}
                    onChange={() => this.props.actionToggleAddImage()}>
                    Add Image
                  </FormCheckbox>
                }
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedEditImage}
                    onChange={() => this.props.toggleEditImage()}>
                    Edit Image
                  </FormCheckbox>
                }
              </DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedAddLine}
                    onChange={() => this.props.actionToggleAddLine()}>
                    Add Line
                    </FormCheckbox>
                }
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedEditLine}
                    onChange={() => this.props.actionToggleEditLine()}>
                    Edit Line
                    </FormCheckbox>
                }
              </DropdownItem>
              
              <DropdownItem header tag="div" className="text-center" ><strong>Design Label</strong></DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedAddLabel}
                    onChange={() => this.props.actionToggleAddLabel()}>
                    Add Label
                  </FormCheckbox>
                }
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedEditLabel}
                    onChange={() => this.props.toggleEditLabel()}>
                    Edit Label
                  </FormCheckbox>
                }
              </DropdownItem>

              <DropdownItem header tag="div" className="text-center" ><strong>Design BIT</strong></DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedAddBitAdjust}
                    onChange={() => this.props.actionToggleAddBitAdjust()}>
                    Add BitAdjust
                  </FormCheckbox>
                }
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedEditBitAdjust}
                    onChange={() => this.props.actionToggleEditBitAdjust()}>
                    Edit BitAdjust
                  </FormCheckbox>
                }
              </DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedAddBitDisplay}
                    onChange={() => this.props.actionToggleAddBitDisplay()}>
                    Add BitDisplay
                    </FormCheckbox>
                }
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedEditBitDisplay}
                    onChange={() => this.props.actionToggleEditBitDisplay()}>
                    Edit BitDisplay
                    </FormCheckbox>
                }
              </DropdownItem>

              <DropdownItem header tag="div" className="text-center" ><strong>Design WORD</strong></DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedAddWordAdjust}
                    onChange={() => this.props.actionToggleAddWordAdjust()}>
                    Add WordAdjust
                    </FormCheckbox>
                }
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedEditWordAdjust}
                    onChange={() => this.props.actionToggleEditWordAdjust()}>
                    Edit WordAdjust
                    </FormCheckbox>
                }
              </DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedAddWordDisplay}
                    onChange={() => this.props.actionToggleAddWordDisplay()}>
                    Add WordDisplay
                  </FormCheckbox>
                }
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedEditWordDisplay}
                    onChange={() => this.props.actionToggleEditWordDisplay()}>
                    Edit WordDisplay
                  </FormCheckbox>
                }
              </DropdownItem>
              
              <DropdownItem header tag="div" className="text-center" ><strong>Design DWORD</strong></DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedAddDWordAdjust}
                    onChange={() => this.props.actionToggleAddDWordAdjust()}>
                    Add DWordAdjust
                    </FormCheckbox>
                }
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedEditDWordAdjust}
                    onChange={() => this.props.actionToggleEditDWordAdjust()}>
                    Edit DWordAdjust
                    </FormCheckbox>
                }
              </DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedAddDWordDisplay}

                    onChange={() => this.props.actionToggleAddDWordDisplay()}>
                    Add DWordDisplay
                    </FormCheckbox>
                }
                {
                  <FormCheckbox toggle small inline
                    checked={this.props.checkedEditDWordDisplay}
                    onChange={() => this.props.actionToggleEditDWordDisplay()}>
                    Edit DWordDisplay
                    </FormCheckbox>
                }
              </DropdownItem>

              <DropdownItem header tag="div" className="text-center"><strong>TURN OFF DESIGN</strong></DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={false}
                    onChange={() => this.props.actionToggleOffAll()}>
                    Off All
                    </FormCheckbox>
                }
              </DropdownItem>
              <DropdownItem header tag="div" className="text-center"><strong>LOGOUT</strong></DropdownItem>
              <DropdownItem>
                {
                  <FormCheckbox toggle small inline
                    checked={false}
                    onChange={() => this.props.actionLogout()}>
                    LogOut
                    </FormCheckbox>
                }
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
        </Nav>
        <AppAsideToggler className="d-md-down-none" />
        {/*<AppAsideToggler className="d-lg-none" mobile />*/}
      </React.Fragment>
    );
  }
}

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;
const mapStateToProps = (state, ownProps) => {
  return {
    nameUser: state.auth.displayName,
    idUser: state.auth.idAdmin,
    checkedAddImage: state.auth.addImage,
    checkedEditImage: state.auth.editImage,
    checkedAddLabel: state.auth.addLabel,
    checkedEditLabel: state.auth.editLabel,
    checkedAddLine: state.auth.addLine,
    checkedEditLine: state.auth.editLine,
    checkedEditWordDisplay: state.auth.editWordDisplay,
    checkedEditWordAdjust: state.auth.editWordAdjust,
    checkedEditDWordDisplay: state.auth.editDWordDisplay,
    checkedEditDWordAdjust: state.auth.editDWordAdjust,
    checkedEditBitDisplay: state.auth.editBitDisplay,
    checkedEditBitAdjust: state.auth.editBitAdjust,
    checkedEditPanel: state.auth.editPanel,
    checkedAddWordDisplay: state.auth.addWordDisplay,
    checkedAddWordAdjust: state.auth.addWordAdjust,
    checkedAddDWordDisplay: state.auth.addDWordDisplay,
    checkedAddDWordAdjust: state.auth.addDWordAdjust,
    checkedAddBitDisplay: state.auth.addBitDisplay,
    checkedAddBitAdjust: state.auth.addBitAdjust,
    checkedAddPanel: state.auth.addPanel,
    checkedEditAll: state.auth.edit,
  }
}


const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionToggleAddImage: () => {
      dispatch(action.actionToggleAddImage())
    },
    toggleEditImage: () => {
      dispatch(action.toggleEditImage())
    },
    actionToggleAddLabel: () => {
      dispatch(action.actionToggleAddLabel())
    },
    toggleEditLabel: () => {
      dispatch(action.toggleEditLabel())
    },
    actionToggleAddLine: () => {
      dispatch(action.actionToggleAddLine())
    },
    actionToggleEditLine: () => {
      dispatch(action.actionToggleEditLine())
    },
    actionToggleEditWordDisplay: () => {
      dispatch(action.actionToggleEditWordDisplay())
    },
    actionToggleEditWordAdjust: () => {
      dispatch(action.actionToggleEditWordAdjust())
    },
    actionToggleEditDWordDisplay: () => {
      dispatch(action.actionToggleEditDWordDisplay())
    },
    actionToggleEditDWordAdjust: () => {
      dispatch(action.actionToggleEditDWordAdjust())
    },
    actionToggleEditBitDisplay: () => {
      dispatch(action.actionToggleEditBitDisplay())
    },
    actionToggleEditBitAdjust: () => {
      dispatch(action.actionToggleEditBitAdjust())
    },
    actionToggleEditPanel: () => {
      dispatch(action.actionToggleEditPanel())
    },

    actionToggleAddWordDisplay: () => {
      dispatch(action.actionToggleAddWordDisplay())
    },
    actionToggleAddWordAdjust: () => {
      dispatch(action.actionToggleAddWordAdjust())
    },
    actionToggleAddDWordDisplay: () => {
      dispatch(action.actionToggleAddDWordDisplay())
    },
    actionToggleAddDWordAdjust: () => {
      dispatch(action.actionToggleAddDWordAdjust())
    },
    actionToggleAddBitDisplay: () => {
      dispatch(action.actionToggleAddBitDisplay())
    },
    actionToggleAddBitAdjust: () => {
      dispatch(action.actionToggleAddBitAdjust())
    },
    actionToggleAddPanel: () => {
      dispatch(action.actionToggleAddPanel())
    },
    actionToggleOffAll: () => {
      dispatch(action.actionToggleOffAll())
    },
    actionLogout: () => {
      dispatch(action.signOut())
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(DefaultHeader);
